.App {
  text-align: center;
  height: 100vh;
  padding: 0 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Grid {
  display: grid;
  grid-auto-flow: column;
  grid-template-rows: repeat(7, 1fr);
  grid-gap: 10px;
  margin-bottom: 50px;
}

.Item button {
  width: 70px;
  height: 65px;
  font-size: 20px;
  background: dodgerblue;
  font-weight: 600;
  color: white;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Item:nth-child(-n + 7) {
  justify-content: start;
}

.Item:nth-child(n + 8) {
  justify-content: end;
}

.Score {
  width: 5px;
  display: flex;
  justify-content: center;
  font-size: 22px;
}

.Counter {
  font-size: 15px;
  align-self: start;
  margin-bottom: -10px;
}

.Reset {
  position: absolute;
  bottom: 50px;
  margin-left: calc(100vw - (100vw/2) - 68px);
}

.Item {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
}

button {
  border: 2px solid black;
}

span {
  font-size: 35px;
}

.Total {
  margin-top: 25px;
}

.Reset button {
  margin-top: 15px;
  font-size: 17px;
  padding: 15px;
  font-weight: bold;
  color: white;
  background: darkred;
  border-radius: 5px;
}

.Data {
  display: flex;
  justify-content: space-around;
  font-weight: bold;
  font-size: 18px;
}